'use strict'

export default class ElasticSearchSource {
  constructor (uri, callback) {
    callback(null, this)
  }
  getInfo (callback) {
    callback(null, {})
  }
  getTile (z, x, y, callback) {
    callback(null)
  }
  static registerProtocols = tilelive => {
    tilelive.protocols['elasticsearch:'] = ElasticSearchSource
  }
}